.TodoSearch__container {
    display: flex;
    justify-content: center;
}

.TodoSearch {
    display: flex;
    align-items: center;
    background: #f9fbfc;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 auto;
    height: 64px;
    width: 80%;
    font-size: 24px;
    text-align: center;
    font-family: "Montserrat";
    font-weight: 400;
    color: #1e1e1f;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
    color: #a5a5a5;
    font-family: "Montserrat";
    font-weight: 400;
}

.TodoSearch:focus {
    outline-color: rgba(50, 38, 224, 0.5);
}
