@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap);
.TodoCounter {
    font-size: 24px;
    text-align: center;
    margin: 0;
    padding: 48px;
}

.TodoSearch__container {
    display: flex;
    justify-content: center;
}

.TodoSearch {
    display: flex;
    align-items: center;
    background: #f9fbfc;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 auto;
    height: 64px;
    width: 80%;
    font-size: 24px;
    text-align: center;
    font-family: "Montserrat";
    font-weight: 400;
    color: #1e1e1f;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::-webkit-input-placeholder {
    color: #a5a5a5;
    font-family: "Montserrat";
    font-weight: 400;
}

.TodoSearch:-ms-input-placeholder {
    color: #a5a5a5;
    font-family: "Montserrat";
    font-weight: 400;
}

.TodoSearch::placeholder {
    color: #a5a5a5;
    font-family: "Montserrat";
    font-weight: 400;
}

.TodoSearch:focus {
    outline-color: rgba(50, 38, 224, 0.5);
}

ul {
  margin: 0;
  padding: 0 0 56px 0;
  list-style: none;
}

.TodoItem {
    display: flex;
    align-items: center;
    background-color: #fafafa;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
}

.Icon-check {
    position: absolute;
    left: 12px;
}
.Icon-check--active {
    color: #af574c;
}

.Icon-delete {
    position: absolute;
    right: 0;
}
.Icon-delete:hover {
    color: rgba(50, 38, 224, 0.5);
}

form {
    width: 90%;
    max-width: 300px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    align-content: center;
    flex-direction: column;
    border-radius: 25px;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: #1e1e1f;
    margin-bottom: 26px;
}

textarea {
    background-color: #f9fbfc;
    border: 2px, solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1e1e1f;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100%-25px);
    border-radius: 25px;
    font-family: "Roboto", sans-serif;
}

textarea::-webkit-input-placeholder {
    color: #a5a5a5;
    font-family: "Montserrat";
    font-weight: 400;
}

textarea:-ms-input-placeholder {
    color: #a5a5a5;
    font-family: "Montserrat";
    font-weight: 400;
}

textarea::placeholder {
    color: #a5a5a5;
    font-family: "Montserrat";
    font-weight: 400;
}

textarea:focus {
    outline-color: #e45959;
}

.todoForm__buttons--container {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.todoForm__button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: "Montserrat";
    border-radius: 10px;
}

.todoForm__button--add {
    background: transparent;
    box-shadow: 0px 5px 25px rgba(240, 206, 155, 0.5);
}

.todoForm__button--cancel {
    background: transparent;
    box-shadow: 0px 5px 25px rgba(240, 206, 155, 0.5);
}
.todoForm__button--cancel:hover {
    background: #fa6161;
    font-weight: bold;
}
.todoForm__button--add:hover {
    background: #fa6161;
    box-shadow: 0px 5px 25px rgba(240, 206, 155, 0.5);
    font-weight: bold;
}

.CreateTodoButton {
    background-color: rgba(50, 38, 224, 0.5);
    box-shadow: 0px 5px 25px rgba(50, 38, 224, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #fafafa;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;

    transform: rotate(0);
    transition: 0.3s ease;
    z-index: 1;
}

.CreateTodoButton:hover {
    transform: rotate(224deg);
}

.ModalBackground {
    background: rgba(32, 35, 41, 0.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

body {
    margin: 0;
    padding: 0;
    background: #f2f4f5;
    color: #1e1e1f;
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
}

#root {
    margin: 0 24px;
    position: relative;
    min-height: 100vh;
}

